/* //////////////////////////////////////////////////GLOBAL///////////////////////////////////////////////////////////// */
@font-face {
  font-family: Manrope;
  src: url(./fonts/manrope.woff);
}
@font-face {
  font-family: Supermarket;
  src: url(./fonts/supermarket.woff);
}
* {
  padding: 0px;
  margin: 0px;
  top: 0px;
}
a {
  cursor: pointer;
}
button {
  cursor: pointer;
}
.border {
  border: 3px solid black;
}
img.background {
  width: 100%;
}
.vertical-center {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.horizontal-center {
  text-align: center;
}
.row-flex {
  display: flex;
  flex-direction: row;
  /* overflow: hidden; */
}
.column-flex {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
p.title {
  font-size: 4vw;
}
.textM {
  font-family: Manrope, Helvetica, Arial, sans-serif;
}
.textS {
  font-family: Supermarket;
}
input {
  border:none
}
textarea {
  border: none;
}
button:hover {
  opacity: 0.8;
}
.fullWidth {
  width: 100%;
}
textarea {
  resize: none;
}
/* //////////////////////////////////////////////////WELCOME///////////////////////////////////////////////////////////// */
h1.title {
  font-family: Manrope, Helvetica, Arial, sans-serif;
  font-size: 9vw;
  padding-top: 0px;
  color: white;
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.7);
}
.container {
  position: relative;
  text-align: center;
}
button.start {
  text-align: center;
  background-color: #9C1402;
  color:white;
  padding: 8px;
  border: none;
  width: 25vw;
  font-size: 3vw;
  border-radius: 20px;
  font-weight: 700;
}
/* //////////////////////////////////////////////////LOGIN///////////////////////////////////////////////////////////// */
button.login {
  width: 10vw;
  color: white;
  border: none;
  border-radius: 8px;
  background-color: #4a362b;
  margin: 1.5vw 1.5vw 0px 1.5vw;
  font-size: 2vw;
  transition: 0.3s;
}
input.login {
  padding:0.5vw 1.2vw 0.5vw 1.2vw;
  margin: 0px 0px 0.8vw 0px;
  width: 100%;
  height: 3vw;
  font-size: 1.5vw;
  text-indent: 2.8vw;
  border-radius: 6px;
  background-color: #ebc27c;
  border: 0.25vw solid #ebc27c;
  box-sizing: border-box;
  background-size: contain;
  background-repeat: no-repeat;
  background-origin: content-box;
}
input.user {  
  background-image: url(./images/user.png);
}
input.error {  
  border: 0.25vw solid red;
}
input.pass {
  background-image: url(./images/lock.png);
}
label.left {
  text-align: left;
  font-size: 2.2vw;
  display: block;
  margin: 0px 0px 0.2vw 0px;
}
div.error {
  font-size: 1.7vw;
}
.tippy-tooltip.error-theme {
  font-family: Supermarket, Helvetica ,Arial, sans-serif;
  color: white;
  background-color: red;
  padding-top: 0px;
  padding-bottom: 0px;
}
.tippy-popper[x-placement=left] .tippy-tooltip.error-theme [x-arrow] {
  border-left: 7px solid red;
}
.tippy-popper[x-placement=right] .tippy-tooltip.error-theme [x-arrow] {
  border-right: 7px solid red;
}
form.form-login-center {
  position: relative;
  left: 30%;
  width: 40%;
  text-align: left;
}
/* //////////////////////////////////////////////////LOGIN///////////////////////////////////////////////////////////// */
div.register-label-box {
  width: 20%;
}
div.form-register-center {  
  width: 55%;
  position: relative;
  left: 22.5%;
  text-align: left;
}
form.form-register-center {
  padding: 2vw;
  background-color: #ebc27c;
  border-radius: 6px;
}
label.register {
  font-size: 1.5vw;
}
input.register {
  /* padding:0.5vw 1.2vw 0.5vw 1.2vw; */
  /* margin: 0px 0px 0.8vw 0px; */
  width: 100%;
  /* height: 3vw; */
  font-size: 1.5vw;
  border: 0.25vw solid white;
}
div.icon {
  justify-content: space-between;
}
.register-center {
  align-self: center;
}
img.icon {
  width: 20%;
  padding-right: 0.2vw;
  /* height: 50%; */
  align-self: center;
}